import React, { Component } from 'react';
import { BrowserRouter, Route } from 'react-router-dom';

import Test from './Test';

export default class App extends Component {
  render(){
    return(
      <div>
        <BrowserRouter>
          <div>
            <Route exact path ='/' component={Test} />
          </div>
        </BrowserRouter>
      </div>
    );
  }
}



